<template>
  <div class="my_cmt">
    <label class="my_h" for="text">发表评论</label>
    <hr>
    <textarea class="my_text" name="" id="text" cols="20" rows="3" placeholder="请输入你要输入的火星语"  v-model="content"></textarea>
    <input class="my_btn" type="button" name="" id=""  value="发表评论"  @click="addComments" >

      <div class="my_cmt_list" >
      <div v-for="(item,i) in comments" :key="item.commentId">
        <div class="my_cmt_title">
          第{{ i+1 }}楼&nbsp用户：{{item.commentUser}}&nbsp&nbsp发表时间：{{item.date | dateFormat}}
        </div>
        <div class="my_cmt_body">
          {{item.commentSays}}
        </div>
      </div>
    </div>
  </div>


</template>

<!--suppress JSAnnotator -->
<script>
        export default {

          data(){
                  return {
                    CommentsAdd:[],
                    content:''
                  }
          },
          created(){
            console.log(22222222)
            // this.addComments()
          },
          methods:{
                  addComments(){
                      console.log(2232131232)
                    console.log(this.comments)
                    console.log(this.content)
                    if (this.content.trim()=="") {
                        Toast('wwww')
                    }else{
                      var message = {
                        commentId:this.comments.length+1,
                        commentUser:"匿名用户",
                        commentDate:"new Date",
                        commentSays:this.content
                      }
                      this.comments.unshift(message)
                      this.content=""
                      // this.comments.

                    }

                  }
          },
          props:['comments']
        }
</script>

<style scoped>
  .my_cmt{
    padding: 0 10px;
    /*box-sizing: border-box;*/
    text-align: left;
  }
  .my_h{
    font-size: 20px;
    color: #000;
    font-family: "Microsoft YaHei";
    font-weight: bolder;
    text-align: left;
    margin-top: 25px;
  }
  [v-cloak]{
    display: none;
  }
  hr{
    /*height: 2px;*/
    background-color: #a49dad;
    margin-top: 10px;
    margin-bottom: 0;
  }
  .my_text{

    margin: 0;
    margin-top: 10px;
    padding: 0;
    font-size: 13px ;
  }
  .my_btn{
    height: 40px;
    width: 100%;
    border-radius: 5px  ;
    margin-top: 0px;
    background-color: #4f5dff;
    color: white;
    font-size: 16px;
  }

  .my_cmt_title{
    margin-top: 5px;
    text-align: left;
    font-size: 12px;
    color: black;
    font-weight: bold;
    background-color: #bdbdbd;
    padding: 5px;
  }
  .my_cmt_body{
    text-align: left;
    padding: 5px;
    background-color: white;
  }
</style>



