 <template>
  <div>
    <!-- 分类滑动区域-->

      <div id="slider" class="mui-slider is-fixed ">
        <div id="sliderSegmentedControl" class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted">
          <div class="mui-scroll">
            <!--<a :to="'/home/imglist/' + item.sortId"   :class="['mui-control-item', flag == 0 ? 'mui-active' : '']" -->
            <a   :class="['mui-control-item', flag == 0 ? 'mui-active' : '']"
               v-for="item in sort" :key="item.sortId + '-info' "  @click="getImgListBySortId(item.sortId)">
              {{ item.sortTitle }}
            </a>
          </div>
        </div>

      </div>

     <!--图片列表区域-->
    <imglistnoslide :imgLists="imgLists"></imglistnoslide>

  </div>
</template>

<script>
  // import mui from "../../../lib/mui/js/mui.js";
   import imglistnoslide from './ImgListNOSlide.vue'
    export default {
      data(){
        return {
          flag:0,
          // 获取顶部滑动栏分类 ,默认为0
          sort:[],
          sortIdSon:'',
          // 获取图片imgLists,  按上面的sort 进行不同的赋值
          imgLists:[]
        };
      },
      created(){
        this.getAllSort();
        // this.getData()
        this.getImgListBySortId(0);
        this.scrollTop()
      },
      mounted(){
        // mui(".mui-scroll-wrapper").scroll({
        //   deceleration: 0.0005 //flick 减速系数，系数越大，滚动速度越慢，滚动距离越小，默认值0.0006
        // });
      },
      methods:{
         getAllSort(){
           //  使用json-server
           this.$http.get('http://localhost:3000/img').then(result=>{
             console.log(result.body)
           // 获取分类
           this.sort = result.body.imgSorts
           // 初始化为所有图片
           // console.log(result.body.imgList)
           // this.imgList=result.body.imgList
         })
           // 用github data
           // this.$http.get('https://raw.githubusercontent.com/wittyzm/webApp/master/data.json').then(result=>{
           //    console.log(result.body.img)
           //    // 获取分类
           //    this.sort = result.body.img.imgSorts
           //    // 初始化为所有图片
           //    console.log(result.body.img.imgList)
           //    this.imgList=result.body.img.imgList
           // })
         },
        getImgListBySortId(sortId){
            // this.flag=1
           // this.$http.get('http://localhost:3000/img'+'?sort='+'sortId').then(result=>{
           this.$http.get('https://raw.githubusercontent.com/wittyzm/webApp/master/data.json').then(result=>{

               // this.imgLists=result.body.imgSort[sortId].sortById
               this.imgLists=result.body.img.imgSort[sortId].sortById
                this.sortIdSon = sortId
                // this.$emit('child-to-far',this.sortIdSon)
            })
            console.log(sortId)
            // 获取传惨
            console.log(this.$route.params)   //{sortId: "0"}
            console.log(this.$router)
            //      修改传参
            this.$router.push({ params:{ sortId : sortId}})

                console.log(11)
        },
        scrollTop(){
          this.$store.commit("windowScrollTop")
        }
      },
      components:{
        "imglistnoslide":imglistnoslide
      }
  }
</script>

<style scoped>
   .mui-slider-indicator.mui-segmented-control{
     position: fixed;
     margin-top: 40px;
    bottom: auto;
    margin-bottom: 0;
    clear: both;
    height: 40px;
    background-color: rgba(255,255,255,.9);
  }

  .photo-list{
    margin-top:40px;
    margin-bottom: 80px;
  }


  .photo-list > li{
    position: relative;
    /*padding-left: 3px;*/
    /*padding-right: 3px;*/
    /*box-shadow:  4px 20px 15px 1px black;*/
    overflow:hidden;
    box-sizing:border-box ;
  }

.photo-list > li >img{
  width: 100%;

}
image[lazy=loading] {
  /*display: block;*/
  width: 20px;
  height: 30px;
  margin: auto;
}
.photo-list > li .info{
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 60px;
  background-color: rgba(0,0,0,.5);
}
.photo-list > li .info .info-title{
  font-size: 20px;
  color: white;
  text-align: left;
}
.photo-list > li .info .info-body{
  font-size: 3px;
  color: white;
  height: 30px;
  text-align: left;
  font-weight: lighter;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
</style>



