<template>
  <div>
    <ul class="mui-table-view">
      <li class="mui-table-view-cell mui-media" v-for="item in imgList" :key="item.imgId">
        <a href="javascript:;">
          <img class="mui-media-object mui-pull-left" :src="item.imgUrl">

          <div class="mui-media-body">
            {{ item.imgTitle }}
            <p class='mui-ellipsis'>{{ item.imgDetail }}</p>
          </div>
        </a>
      </li>



    </ul>
  </div>
</template>

<script>
    export default {
      data(){
          return {
            imgList:[]
          }
      },
      created(){
        this.getImgList()
      },
      methods:{
        getImgList(){
          console.log(localStorage.favoriteList)
          console.log(typeof localStorage.favoriteList)
          this.imgList = JSON.parse(localStorage.getItem("favoriteList"))

          console.log(this.imgList)
        }
      }
    }
</script>

<style scoped>
.mui-media-body{
  text-align: left;
}
</style>



