import Vue from 'vue';

import Vuex from 'vuex';

import persistedState from 'vuex-persistedstate';
// 受用Vue
Vue.use(Vuex)

// 创建vuex 实例
const store = new Vuex.Store({
    state:{
      count:0,
      imgObjById:{},
      comments:{},
      favoriteImg:[]
    },
    mutations:{
      addImgObj(state,para1){
        console.log(state)
        state.imgObjById = para1

        console.log(state)
        console.log(state.imgObjById)
        console.log(state.imgObjById.para1)
        console.log(state.imgObjById.para1.imgUrl)
        console.log(localStorage)

      },
      addComments(state,comment){
        state.comments = comment
        console.log(412424341331)
        console.log(state.comments)
      },
      windowScrollTop(){
          window.scrollTo(0,0);

      },
      countAdd(state){
        state.count++
      },
      countIncrement(state){
        state.count--
      },
      countToBe(state,dat1){
        state.count = dat1
      },
      clearCount(state){
        state.count=0
      }
    },
    plugins: [persistedState()]
})

// 导出store
export default store



