<script>
    export default {
        name: "PersonContainer",
      methods:{
        wannaToExit(){
          var text = "天气这么热讲个故事吧从前 有个龙王招女婿 要求女婿必须90斤一个乌龟来了 称重正好89斤只好伤心离开" +
            "离开的路上遇到了小虾米小虾米了解事情经过后说我们两个加起来正好1斤便躲在乌龟的耳朵里乌龟回去再次称重正好9" +
            "0斤龙王问它 你怎么突然重了1斤乌龟一紧张 把小虾掉出来了龙王问小虾在干什么小虾说我在给王八讲故事";
          var arr = []
          var j = 0;
          console.log(text)
          console.log(arr)
          for(var i = 0 ; i<=30 ;i++){
            j=i*5;
            // arr.push(text.substring(j,j+5))
            alert(text.substring(j,j+5))
          }
          // console.log(arr)
        }
      }
    }

</script>

<style scoped>
.mui-table-view-cell{

}
</style>



