<template>
  <div>

    <transition
      @before-enter="beforeEnter"
      @enter="enter"
      @after-enter="afterEnter">
      <div class="ball_container" v-show="ballFlag" id="ball" ref="ball"></div>
    </transition>


    <div class="mui-card">
      <div class="mui-card-header mui-card-media">
        <img :src="this.$store.state.imgObjById.para1.imgUrl " />
        <div class="mui-media-body">
          {{ this.$store.state.imgObjById.para1.imgTitle }}
          <p>{{ new Date() | dateFormat }}</p>
        </div>
        <!--<img class="mui-pull-left" src="../imagses/logo.png" width="34px" height="34px" />
                  <h2>小M</h2>
                  <p>发表于 2016-06-30 15:30</p>-->
      </div>
      <div class="mui-card-content"  >
        <img :src="this.$store.state.imgObjById.para1.imgUrl " alt="" width="100%"/>
      </div>

      <div class="mui-card-footer">
        <!--<a class="mui-card-link">Like</a>-->
        <a @click="ballAdd" id="heart"><span class="mui-icon-extra mui-icon-extra-heart-filled"></span></a>
        <!--<a class="mui-card-link">Comment</a>-->
        <!--<a class="mui-card-link">{{this.$store.state.count}}</a>-->
        <a class="mui-card-link">comment</a>
      </div>
    </div>
    <comments :comments="comments"></comments>

  </div>
</template>

<!--suppress JSAnnotator -->
<script>
  import comments from '../../subComponents/Comments.vue'
export default {
      name: "ImgInfo",
      data(){
        // noinspection JSAnnotator
        return {
          msg:'msg',
          ballFlag:false,
          heartRed:false,
          comments:[],
          imgFavorite:[],
          localFavorite:''

        }
      },
      created(){
        console.log(this.$store.state.imgObjById)
        this.getComments()
          this.scrollTop()

        // 检验小球颜色
        this.ifShowRed()

      },
      methods:{
        getComments(){
          this.comments = this.$store.state.comments
          console.log(this.comments)
        },
        scrollTop(){
          this.$store.commit("windowScrollTop")
        },
        ballAdd(){
          this.ballFlag = !this.ballFlag
        },
        ifShowRed(){
          // if (localStorage.favoriteList=="" ) {
          //   this.localFavorite=[];
          // } else {
          //   this.localFavorite = JSON.parse(localStorage.favoriteList)
          // }
          // // 如果可以找到同样的图片，则显示红色。否则绿色
          // const indexofobj1=this.localFavorite.findIndex( value1 => {
          //   return value1.imgUrl == this.$store.state.imgObjById.para1.imgUrl
          // })
          // if (indexofobj1 >= 0){
          //   this.heartRed = true
          //   var heartColor1 = this.heartRed?"red":"#007aff"
          //   // 此时为heartRed =false.  ,让它变为true
          //
          //   /* false = blue  --> 变红---> 加一*/
          //   /* true = red  --> 变蓝---> 贱一*/
          //   $('#heart').css("color",heartColor1);
          // }
          // console.log(indexofobj1)
        },
        enter(el){
          el.offsetWidth

          // 小球移动后的距离
          el.style.transform = "translate(205px,"+ (282+$(window)[0].pageYOffset) +"px)"
          console.log(el.style.transform)
          el.style.transition = "all 0.5s cubic-bezier(1,.09,.5,.94)";

          // console.log(this.$refs)
          // console.log($('#heart')[0].offsetHeight)
          // console.log($('#heart')[0].offsetTop)
          // console.log($(window))
          // console.log($(window)[0].pageYOffset)
          //
          // console.log($('#heart'))
          // 加 done 报错
          // done();
        },
        beforeEnter(el){
          if (!this.heartRed) {
            el.style.transform = "translate(0,0)";  // 如果蓝色，变为起点

          } else{  // 为真，已经红了
            // this.heartRed=!this.heartRed
            el.style.transform = "translate(205px,"+ (282+$(window)[0].pageYOffset) +"px)"
          }

        },
        afterEnter(el){
          this.ballFlag = !this.ballFlag
          // 为假变为红色。为真蓝色然后  // 本来为假德华，就要变成红色，  检验的话，则为真就是红色
          var heartColor = this.heartRed?"#007aff":"red"
          // 此时为heartRed =false.  ,让它变为true

          /* false = blue  --> 变红---> 加一*/
          /* true = red  --> 变蓝---> 贱一*/
          $('#heart').css("color",heartColor);
          if (!this.heartRed) { //
              this.heartRed=!this.heartRed// 改变heartRed


              // 1 首先设置一个storage   local叫做 favoriteList， 组件内叫做localFavorite
              console.log(localStorage.favoriteList )  // 初始为空
              // 如果内存中为空，则输出一个[]  ,如果不为空，则输出字符串，并转为数组对象，然后拼接想要的对象，在存进storage
              if (localStorage.favoriteList=="" ) {
                this.localFavorite=[];
                this.$store.commit("clearCount")
              } else {
                this.localFavorite = JSON.parse(localStorage.favoriteList)
              }

              // 如果可以找到同样的图片，则不给保存。否则可以保存
            const indexofobj=this.localFavorite.findIndex( value => {
              return value.imgUrl == this.$store.state.imgObjById.para1.imgUrl
            })

            // console.log(indexofobj)
            //
            // console.log(this.localFavorite)   // 数组
            // console.log(this.$store.state.imgObjById.para1)  // 对象
            // console.log(this.$store.state.imgObjById.para1.imgUrl)  // 对象
            if (indexofobj < 0) {
              this.localFavorite.unshift(this.$store.state.imgObjById.para1);
            } else {
              this.localFavorite.splice(indexofobj,1)

            }





            localStorage.setItem("favoriteList",JSON.stringify(this.localFavorite))
              // console.log(this.localFavorite)
          } else {
             // 刚进来就是该为红色的 flagred=true

              // this.$store.state.count-=1
            var heartColor = this.heartRed?"#007aff":"red"
            // 此时为heartRed =false.  ,让它变为true

            /* false = blue  --> 变红---> 加一*/
            /* true = red  --> 变蓝---> 贱一*/
            $('#heart').css("color",heartColor);
            this.heartRed=!this.heartRed// 改变heartRed
          }
          this.$store.commit("countToBe",this.localFavorite.length)
        }
      },
      components:{
          "comments":comments
      }
    }
</script>

<style scoped>
  [v-cloak]{
    display: none;
  }
  .ball_container{

    color: darkgray;
    /*transform: translate(40px,600px);*/
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: red;
    position: absolute;
    left: 27px;
    top: 320px;
    z-index: 44;

  }

</style>



