import Vue from 'vue'
import VueRouter from 'vue-router'

// 导入对应的路由组件
import HomeContainer from '@/components/tabbar/HomeContainer'
import HobbiesContainer from '@/components/tabbar/HobbiesContainer'
import FavoriteContainer from '@/components/tabbar/FavoriteContainer'
import PersonContainer from '@/components/tabbar/PersonContainer'
import ImgList from '@/components/home/img/ImgList'
import ImgListNoSlide from '@/components/home/img/ImgListNoSlide'
import ImgInfo from '@/components/home/img/ImgInfo'

Vue.use(VueRouter)

// 暴漏路由对象给 main.js的
export default new VueRouter({
  routes: [
    { path : '/',redirect:'/home' },
    { path : '/home' , component: HomeContainer},
    { path : '/home/imglist/:sortId' , component: ImgList},
    // { path : '/home/imglist/:sortId' , component: ImgListNoSlide},
    { path : '/home/imginfo/:id' , component: ImgInfo},
    { path : '/hobbies' , component: HobbiesContainer},
    { path : '/favorite' , component: FavoriteContainer},
    { path : '/person' , component: PersonContainer}
  ],
  linkActiveClass: 'mui-active' // 覆盖默认的路由高亮的类，默认的类叫做 router-link-active
})



