<template>
  <div id="app">
     <!-- 头部 -->
    <div id="header">
      <mt-header title="My Pro" class="is-fixed">
        <router-link to="/" slot="left">
          <mt-button class="my_back" icon="back" @click="$router.back(-1)">返回</mt-button>
        </router-link>
        <!--<mt-button icon="more" slot="right"></mt-button>-->
      </mt-header>
    </div>

    <!-- 中间变换 -->
    <div id="center">
      <transition>
        <router-view>

        </router-view>
      </transition>
    </div>


    <!-- 底部不变 -->
    <div id="footer">
      <mt-tabbar fixed  id="mt-tabbar">
        <router-link to="/home" tag="div">
          <mt-tab-item id="主页">
            <span class="glyphicon glyphicon-home" aria-hidden="true"></span>
            <span>Home</span>
          </mt-tab-item>
        </router-link>
        <router-link to="/hobbies" tag="div">
          <mt-tab-item id="乐趣">
            <span class="glyphicon glyphicon-plane" aria-hidden="true"></span>
            <span>Learning</span>
          </mt-tab-item>
        </router-link>
        <router-link to="/favorite" tag="div">
          <mt-tab-item id="收藏" class="favorite">
            <a><span class="mui-icon-extra mui-icon-extra-heart-filled"></span><span class="favor_count">{{this.$store.state.count}}</span></a>
            <span>Favorite</span>

          </mt-tab-item>
        </router-link>
        <router-link to="/person" tag="div">
          <mt-tab-item id="个人">
            <a id="icon-icon-contact"><span class="mui-icon mui-icon-contact"></span></a>
            <span>Person</span>
          </mt-tab-item>
        </router-link>
      </mt-tabbar>
    </div>


  </div>
</template>

<script>

export default {

  data () {
    return {
      msg:1,
      lunBoList:[] // 保存轮播图数组
    }
  },
  created(){
    this.getComments()
    localStorage.setItem("favoriteList",""),
      this.$Store.commit("cleatCount")
  },
  methods:{
    getComments(){
      this.$http.get('http://localhost:3000/comments').then(res => {
        console.log(res.body)
        this.$store.commit('addComments',res.body)
      })

    }
  }
}
</script>

<style lang="">
  [v-cloak]{
    display: none;
  }
  *{
    margin: 0;
    padding: 0;

  }
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: 0;
  padding: 0;

}
#header{
  height: 40px;
}
 .mint-button-icon{
   margin-top: 15px;
   /*padding-top: 10px;*/
   padding-left: 10px;
 }
  .mintui-back:before{
    /*margin-top: 3px;*/
    position: absolute;
    left: 0;
    top: 10px;
  }
  label{
    margin-bottom: 0;
  }


  /* 轮播图*/
  #swipeMy{
    height: 180px;
    width: 100%;

  }
  #swipeMy img{
    width: 100%;


  }

  /* center  */
#center{
  /*margin-top: 60px;*/
  margin-bottom: 80px;
}
  /* center 图标和字体设置*/
  #center ul li span {
    font-size: 30px;
  }
  #center ul li:nth-child(1) span {
    color: blueviolet;
  }
  #center ul li:nth-child(2) span {
    color: greenyellow;
  }
  #center ul li:nth-child(3) span {
    color: red;
  }
  #center ul li:nth-child(4) span {
    color: gray;
  }
  #center ul li:nth-child(5) span {
    color: indigo;
  ;
  }


  /* 底部通栏*/
  #footer  .mint-tabbar.is-fixed{

    right: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    z-index: 1;
    height: 80px;
    background-color: #26a2ff;
    display: flex;
    justify-content: space-around;

  }

  .mint-tab-item{
    height: 80px;
  }

  /* footer 图标设置 t*/
  .mint-tab-item-label  span:nth-child(1){

    text-align: center;
    line-height: 30px;
    color: white;
    height: 30px;
    width: 100%;
    font-size: 30px;
  }
  /* footer 字体设置*/
.mint-tab-item-label  span:nth-child(2){
  padding-top: 5px;
  color: whitesmoke;
  font-weight: 700;
  font-size: 15px;
  display: block;
  margin-top: 5px;
  text-decoration: none;
}
  .mint-tab-item-label{
    padding-top: 10px;

  }
  .favorite{
    position: relative;
  }
  .favorite .favor_count{
    position: absolute;
    top: 10px;
    right: 10px;
    height: 20px;
    width: 20px;
    text-align: center;
    line-height: 10px;
    background-color: red;
    border-radius: 50%;
    /*display: block;*/
    padding: 0;
  }

  .v-enter{
    opacity: 0;
    transform: translateX(100%);

  }

  .v-leave-to{
    opacity: 0;
    transform: translateX(-100%);
    position: absolute;
  }
  .v-enter-active,
  .v-leave-active{
    transition: all 0.5s ease;
    /*position: absolute;*/
  }
</style>



