<template>

  <div>
    <ul class="photo-list">
      <router-link v-for="item in imgLists" :key="item.imgId+'_a' " :to="'/home/imginfo/' + item.imgId" tag="li"  @click.native="imgObj(item)">
        <!--{{item }}-->
        <img v-lazy="item.imgUrl" >
        <div class="info">
          <h1 class="info-title">{{ item.imgTitle }}</h1>
          <div class="info-body">{{ item.imgDetail }}</div>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
    export default {
      data(){
        return {

        }
      },
      created(){
          this.scrollTop()
      },
      methods:{
        imgObj(para1){
          this.$store.commit("addImgObj",{para1})
          console.log(this.$store.state.imgObjById)
        },
        scrollTop(){
          this.$store.commit("windowScrollTop")
        }
      },
      props: ["imgLists"]
    }
</script>

<style scoped>
  [v-cloak]{
    display: none;
  }
  .mui-slider-indicator.mui-segmented-control{
    position: fixed;
    margin-top: 40px;
    bottom: auto;
    margin-bottom: 0;
    clear: both;
    height: 40px;
    background-color: rgba(255,255,255,.9);
  }

  .photo-list{
    margin-top:40px;
    margin-bottom: 80px;
  }


  .photo-list > li{
    position: relative;
    /*padding-left: 3px;*/
    /*padding-right: 3px;*/
    /*box-shadow:  4px 20px 15px 1px black;*/
    overflow:hidden;
    box-sizing:border-box ;
  }

  .photo-list > li >img{
    width: 100%;

  }
  image[lazy=loading] {
    /*display: block;*/
    width: 20px;
    height: 30px;
    margin: auto;
  }
  .photo-list > li .info{
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 60px;
    background-color: rgba(0,0,0,.5);
  }
  .photo-list > li .info .info-title{
    font-size: 20px;
    color: white;
    text-align: left;
  }
  .photo-list > li .info .info-body{
    font-size: 3px;
    color: white;
    height: 30px;
    text-align: left;
    font-weight: lighter;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
</style>



